import React from "react";
import { render, fireEvent } from "@testing-library/react-native";
import Header from "../../../src/components/layout/Header";
import { Text } from "react-native";

jest.mock('@expo/vector-icons');

// Mock de useNavigation de React Navigation
jest.mock("@react-navigation/native", () => ({
  useNavigation: () => ({
    canGoBack: () => true,
    goBack: jest.fn(),
  }),
}));

describe("Header component", () => {
  it("affiche le titre passÃ© en props", () => {
    const { getByText } = render(<Header title="Accueil" />);
    expect(getByText("Accueil")).toBeTruthy();
  });

  it("appelle onBackPress quand on clique sur le bouton retour", () => {
    const onBackPress = jest.fn();
    const { getByRole } = render(<Header title="Retour" onBackPress={onBackPress} />);
    fireEvent.press(getByRole("button"));
    expect(onBackPress).toHaveBeenCalled();
  });

  it("affiche le bouton droit avec icÃ´ne et dÃ©clenche onRightPress", () => {
    const onRightPress = jest.fn();
    const { getAllByRole } = render(
      <Header title="Actions" rightIcon="settings" onRightPress={onRightPress} />
    );
    const buttons = getAllByRole("button");
    fireEvent.press(buttons[1]); // bouton droit
    expect(onRightPress).toHaveBeenCalled();
  });

  it("n'affiche pas le bouton retour si showBackButton est false", () => {
    const { queryByRole } = render(<Header title="Sans retour" showBackButton={false} />);
    expect(queryByRole("button")).toBeNull();
  });

  it("affiche le composant custom Ã  gauche (leftComponent)", () => {
    const { getByTestId, queryByRole } = render(
      <Header title="Custom" leftComponent={<Text testID="custom-left">ðŸ‘ˆ</Text>} />
    );
    expect(getByTestId("custom-left")).toBeTruthy();
    expect(queryByRole("button")).toBeNull(); // le bouton retour n'est pas rendu
  });

  it("affiche les enfants si children est fourni et ignore le contenu standard", () => {
    const { getByTestId, queryByText } = render(
      <Header title="Ignore ce titre">
        <Text testID="custom-child">ðŸ”§ Custom Child</Text>
      </Header>
    );
    expect(getByTestId("custom-child")).toBeTruthy();
    expect(queryByText("Ignore ce titre")).toBeNull();
  });

  it("affiche le titre large et le sous-titre en mode largeTitle", () => {
    const { getByText } = render(
      <Header title="Grand titre" subtitle="Sous-titre" largeTitleMode />
    );
    expect(getByText("Grand titre")).toBeTruthy();
    expect(getByText("Sous-titre")).toBeTruthy();
  });
});
